import Matter from 'matter-js';

import shapeChat from '@/assets/media/SHAPE-CHAT.png';
import shape404DarkSquare from '@/assets/media/SHAPE-404-DARK-SQUARE.png';
import shapeAttention from '@/assets/media/SHAPE-ATTENTION.png';
import shapeUiUx from '@/assets/media/SHAPE-UIUX.png';
import shapeDarkBlue from '@/assets/media/SHAPE-DARKBLUE.png';
import shapeBlue from '@/assets/media/SHAPE-BLUE.png';
import shapeDev from '@/assets/media/SHAPE-DEV.png';
import shapeCircle from '@/assets/media/SHAPE-CIRCLE.png';
import shapeBitrix from '@/assets/media/SHAPE-BITRIX.png';
import shapeCode from '@/assets/media/SHAPE-CODE.png';
import shapeErrorSquare from '@/assets/media/SHAPE-ERROR-SQUARE.png';


const SHAPES = [
  {path: shapeDarkBlue, width: 216, height: 93},
  {path: shapeCode, width: 130, height: 73},
  {path: shapeBlue, width: 116, height: 75},
  {path: shapeErrorSquare, width: 214, height: 71},
  {path: shape404DarkSquare, width: 204, height: 83},
  {path: shapeBitrix, width: 317, height: 84},
  {path: shapeDev, width: 138, height: 64},
  {path: shapeUiUx, width: 214, height: 81},
  {path: shapeCircle, width: 26, height: 26},
  {path: shapeChat, width: 215, height: 149},
  {path: shapeAttention, width: 96, height: 84},
];

const {
  Engine,
  Render,
  Runner,
  World,
  Bodies,
  Common,
} = Matter;

const findNodes = () => ({
  canvasWrapper: document.querySelector('[data-anim-entity="intro-shapes-canvas-wrapper"]'),
  canvas: document.querySelector('[data-anim-entity="intro-shapes-canvas"]'),
});

const randomX = (width) => Common.random(0, width);
const randomY = (height) => Common.random(-height / 2, height / 8);

const calcCanvasSize = (containerNode) => {
  if (!containerNode) {
    throw new Error('errorPage.js:calcCanvasSize: containerNode is null');
  }
  const width = containerNode.offsetWidth;
  const height = containerNode.offsetHeight;

  return {
    width,
    height,
  };
};

const generateStaticShapes = ({ canvasWidth, canvasHeight }) => {
  const canvasXOffset = 320;
  const canvasYOffset = 160;
  const shapeStrokeWidth = 1;

  const groundWidth = canvasWidth + canvasXOffset;
  const groundHeight = 160;
  const groundX = (canvasWidth / 2) + canvasXOffset / 2;
  const groundY = canvasHeight + canvasYOffset / 2 + shapeStrokeWidth;

  const wallWidth = 160;
  const wallHeight = canvasHeight;
  const wallY = canvasHeight / 2;
  const wallLeftX = -wallWidth / 2 - shapeStrokeWidth;
  const wallRightX = canvasWidth + wallWidth / 2 + shapeStrokeWidth;

  const ground = Bodies.rectangle(groundX, groundY, groundWidth, groundHeight, { isStatic: true });
  const wallLeft = Bodies.rectangle(wallLeftX, wallY, wallWidth, wallHeight, { isStatic: true });
  const wallRight = Bodies.rectangle(wallRightX, wallY, wallWidth, wallHeight, { isStatic: true });

  return [
    ground,
    wallLeft,
    wallRight,
  ];
};

const generateShapes = ({ canvasWidth, canvasHeight }) => (
  SHAPES.map((props) => {
    const x = randomX(canvasWidth);
    const y = randomY(canvasHeight);

    return Bodies.rectangle(x, y, props.width, props.height, {
      render: { sprite: { texture: props.path, xScale: 1, yScale: 1 } },
    });
  }));

const configureAnimation = () => {
  const {
    canvasWrapper,
    canvas,
  } = findNodes();

  const {
    width,
    height,
  } = calcCanvasSize(canvasWrapper);

  const engine = Engine.create();
  engine.gravity.y = 2;

  const render = Render.create({
    element: canvas,
    engine,
    options: {
      width,
      height,
      wireframes: false,
      background: 'transparent',
      pixelRatio: 2,
    },
  });

  const staticShapes = generateStaticShapes({ canvasWidth: width, canvasHeight: height });
  const shapes = generateShapes({ canvasWidth: width, canvasHeight: height });

  World.add(engine.world, [...staticShapes, ...shapes]);

  return {
    engine,
    render,
  };
};

const runAnimation = ({
  engine,
  render,
}) => {
  Runner.run(engine);
  Render.run(render);
};

const mainIntroAnimation = () => {
  try {
    const {
      engine,
      render,
    } = configureAnimation();

    runAnimation({
      engine,
      render,
    });
  } catch (e) {
    console.debug(e);
  }
};

export default mainIntroAnimation;
