addEventListener('DOMContentLoaded', function (){
    const smoothLinks = document.querySelectorAll('a[href*="#"]');
    for (let smoothLink of smoothLinks) {
        smoothLink.addEventListener('click', function (e) {
            const id = smoothLink.getAttribute('href');
            e.preventDefault();

            if(id != "#") {
                document.querySelector(id).scrollIntoView({
                    behavior: 'smooth',
                    block: 'start'
                });
            }
        });
    };
})

const link = (id) => {
    let pathname = location.pathname.replaceAll('/', '');
    let hash = location.hash.replaceAll('#', '');
    let currentId = id ? `#${id}` : (hash != '' ? hash : `#${pathname}`);

    if (currentId != '' && currentId != '#'){
        let element = document.querySelector(currentId);

        const run = () => {
            element.scrollIntoView({
                behavior: 'smooth',
                block: 'start'
            });
        }
    
        if(currentId != '#'  && currentId != ''  && document.documentElement.contains(element)) {
            run()
        } 
    }
}

document.addEventListener('DOMContentLoaded', () => {
    link()
})

export default link;