import Swiper from 'swiper';
// import detailLandingAnimation from '@/init/common/animation/detailLanding.js';

export default () => {
  const slidersComponents = Array.from(document.querySelectorAll('[data-component="r-slider"]'));

  slidersComponents.forEach((slidersComponent) => {
    const sliderNode = slidersComponent.querySelector('[data-entity="slider"]');
    new Swiper(sliderNode, {
      slidesPerView: 'auto',
      spaceBetween: 40,
      centeredSlides: true,
      loop: true,
    });
  });

  // detailLandingAnimation();
};
