import Matter from 'matter-js';

import shapeError from '@/assets/media/SHAPE-ERROR.png';
import shape404Dark from '@/assets/media/SHAPE-404-DARK.png';
import shape404Blue from '@/assets/media/SHAPE-404-BLUE.png';
import shapeSad from '@/assets/media/SHAPE-SAD.png';
import shapeYellow from '@/assets/media/SHAPE-YELLOW.png';
import shapeOrange from '@/assets/media/SHAPE-ORANGE.png';

const SHAPES_WIDTH = 436;
const SHAPES_HEIGHT = 176;
const SHAPES_RADIUS = 90;

const SHAPES_TEXTURES = [
  shape404Dark,
  shapeError,
  shape404Blue,
  shapeSad,
  shapeYellow,
  shapeOrange,
];

const {
  Engine,
  Render,
  Runner,
  World,
  Bodies,
  Common,
} = Matter;

const findNodes = () => ({
  canvasWrapper: document.querySelector('[data-anim-entity="error-shapes-canvas-wrapper"]'),
  canvas: document.querySelector('[data-anim-entity="error-shapes-canvas"]'),
});

const randomX = (width) => Common.random(0, width);
const randomY = (height) => Common.random(-height / 2, height / 8);

const calcCanvasSize = (containerNode) => {
  if (!containerNode) {
    throw new Error('errorPage.js:calcCanvasSize: containerNode is null');
  }
  const width = containerNode.offsetWidth;
  const height = containerNode.offsetHeight;

  return {
    width,
    height,
  };
};

const generateStaticShapes = ({ canvasWidth, canvasHeight }) => {
  const canvasXOffset = 320;
  const canvasYOffset = 160;
  const shapeStrokeWidth = 1;

  const groundWidth = canvasWidth + canvasXOffset;
  const groundHeight = 160;
  const groundX = (canvasWidth / 2) + canvasXOffset / 2;
  const groundY = canvasHeight + canvasYOffset / 2 + shapeStrokeWidth;

  const wallWidth = 160;
  const wallHeight = canvasHeight;
  const wallY = canvasHeight / 2;
  const wallLeftX = -wallWidth / 2 - shapeStrokeWidth;
  const wallRightX = canvasWidth + wallWidth / 2 + shapeStrokeWidth;

  const ground = Bodies.rectangle(groundX, groundY, groundWidth, groundHeight, { isStatic: true });
  const wallLeft = Bodies.rectangle(wallLeftX, wallY, wallWidth, wallHeight, { isStatic: true });
  const wallRight = Bodies.rectangle(wallRightX, wallY, wallWidth, wallHeight, { isStatic: true });

  return [
    ground,
    wallLeft,
    wallRight,
  ];
};

const generateShapes = ({ canvasWidth, canvasHeight }) => (
  SHAPES_TEXTURES.map((texturePath) => {
    const x = randomX(canvasWidth);
    const y = randomY(canvasHeight);

    return Bodies.rectangle(x, y, SHAPES_WIDTH, SHAPES_HEIGHT, {
      chamfer: { radius: SHAPES_RADIUS },
      render: { sprite: { texture: texturePath, xScale: 1, yScale: 1 } },
    });
  }));

const configureAnimation = () => {
  const {
    canvasWrapper,
    canvas,
  } = findNodes();

  const {
    width,
    height,
  } = calcCanvasSize(canvasWrapper);

  const engine = Engine.create();
  engine.gravity.y = 2;

  const render = Render.create({
    element: canvas,
    engine,
    options: {
      width,
      height,
      wireframes: false,
      background: 'transparent',
      pixelRatio: 2,
    },
  });

  const staticShapes = generateStaticShapes({ canvasWidth: width, canvasHeight: height });
  const shapes = generateShapes({ canvasWidth: width, canvasHeight: height });

  World.add(engine.world, [...staticShapes, ...shapes]);

  return {
    engine,
    render,
  };
};

const runAnimation = ({
  engine,
  render,
}) => {
  Runner.run(engine);
  Render.run(render);
};

const errorPageAnimation = () => {
  try {
    const {
      engine,
      render,
    } = configureAnimation();

    runAnimation({
      engine,
      render,
    });
  } catch (e) {
    console.debug(e);
  }
};

export default errorPageAnimation;
