class Marquee {
  static normalizeClonedItemNode(clonedItemNode) {
    const imgNodes = Array.from(clonedItemNode.querySelectorAll('img'));

    if (!imgNodes.length) return clonedItemNode;

    imgNodes.forEach((imgNode) => {
      const imgSrc = imgNode.dataset.src;
      imgNode.classList.remove('lozad');

      if (imgSrc) {
        // eslint-disable-next-line no-param-reassign
        imgNode.src = imgSrc;
      }
    });

    // eslint-disable-next-line consistent-return
    return clonedItemNode;
  }

  constructor(containerNode, gsap) {
    this.gsap = gsap;
    this.containerNode = containerNode;

    try {
      this.findNodes();
      this.configure();
      this.initComponent();
    } catch (e) {
      console.debug(`[SPIKS] Marquee: ${e}`);
    }
  }

  findNodes() {
    this.itemNode = this.containerNode.querySelector('[data-entity="item"]');

    if (!this.itemNode) {
      throw new Error('[SPIKS] Marquee: textNode is null');
    }
  }

  configure() {
    const RATE = 50;

    const itemInnerWidth = this.itemNode.clientWidth;
    const itemStyles = window.getComputedStyle(this.itemNode);
    const itemMarginRight = parseInt(itemStyles.marginRight, 10) || 0;

    this.containerWidth = this.containerNode.getBoundingClientRect().width;
    this.itemWidth = itemInnerWidth + itemMarginRight / 2.5;
    this.duration = this.itemWidth / RATE;
  }

  calcItemsCount(repeatCount) {
    if (this.itemWidth < this.containerWidth) {
      const itemsCount = Math.round(this.containerWidth / this.itemWidth);
      return itemsCount * repeatCount;
    }

    return repeatCount;
  }

  attachAnimation() {
    this.gsap.to(this.containerNode, this.duration, {
      repeat: -1,
      x: `-${this.itemWidth}`,
      ease: 'linear',
    });
  }

  initComponent() {
    this.repeatCount = this.calcItemsCount(1);

    for (let index = 0; index < this.repeatCount; index += 1) {
      const cloneItemNode = this.itemNode.cloneNode(true);
      const normalizedClonedItemNode = Marquee.normalizeClonedItemNode(cloneItemNode);
      this.containerNode.append(normalizedClonedItemNode);
    }

    this.attachAnimation();
  }
}

export default Marquee;
