import { ScrollTrigger } from 'gsap/ScrollTrigger';

import mainIntroAnimation from '@/init/common/animation/mainIntro.js';

const mainLandingAnimation = (gsap) => {
  const findElements = () => {
    const introLogo = document.querySelector('[data-anim-entity="intro-logo"]');
    const introSubtitle = document.querySelector('[data-anim-entity="intro-subtitle"]');
    const introTitleParts = Array.from(document.querySelectorAll('[data-anim-entity="intro-title-row"]'));
    const introButton = document.querySelector('[data-anim-entity="intro-button"]');
    const browser = document.querySelector('[data-anim-entity="browser"]');
    const browserSection = document.querySelector('[data-anim-entity="browser-section"]');
    const projectsSection = document.querySelector('[data-anim-entity="projects"]');
    const projectsTitle = document.querySelector('[data-anim-entity="projects-title"]');
    const projectsItems = Array.from(document.querySelectorAll('[data-anim-entity="project"]'));

    return {
      introTitleParts,
      introLogo,
      introSubtitle,
      introButton,
      browser,
      browserSection,
      projectsSection,
      projectsTitle,
      projectsItems,
    };
  };

  const renderIntro = (elements) => {
    const {
      introTitleParts,
      introButton,
      browser,
      browserSection,
      introLogo,
      introSubtitle,
      projectsSection,
    } = elements;

    const timeline = gsap.timeline();

    timeline.add(gsap.from(introLogo, {
      duration: 2,
      translateY: '75%',
      opacity: 0,
      scale: 0.85,
    }))

    timeline.add(gsap.from(browser, {
      duration: 2.4,
      translateY: '100px',
      opacity: 0,
      onStart: () => {
        mainIntroAnimation();
      },
    }), '-=1.6');

    if(introSubtitle){
    timeline.add(gsap.from(introSubtitle, {
      duration: 1,
      translateY: '25%',
      opacity: 0,
      }), '-=1.9')
    }

    introTitleParts.forEach((introTitlePart, index) => {
      if (index) {
        timeline.add(gsap.from(introTitlePart, {
          duration: 2,
          translateY: '50%',
          opacity: 0,
        }), '-=1.9');

        return;
      }

      timeline.add(gsap.from(introTitlePart, {
        duration: 2,
        opacity: 0,
        translateY: '50%',
      }), '-=1.4');
    });

    timeline.add(gsap.from(introButton, {
      duration: 2,
      opacity: 0,
    }), '-=1.9');

    gsap.to(browserSection, {
      scrollTrigger: {
        trigger: projectsSection,
        start: 'top 75%',
        scrub: 1,
      },

      y: '-=200',
    });
  };

  const renderProjectsSection = (elements) => {
    const {
      projectsTitle, projectsItems,
    } = elements;

    gsap.to(projectsTitle, {
      opacity: 1,
      scale: 1,
      pointerEvents: 'all',
      scrollTrigger: {
        trigger: projectsTitle,
        start: 'top center',
      },
    });

    ScrollTrigger.batch(projectsItems, {
      onEnter: (cards) => {
        gsap.to(cards, {
          opacity: 1,
          scale: 1,
          pointerEvents: 'all',
          stagger: { each: 0.2 },
        });
      },
      start: 'top center',
    });
  };

  try {
    const elements = findElements();

    renderIntro(elements);
    renderProjectsSection(elements);
  } catch (e) {
    console.debug(e);
  }
};

export default (gsap) => mainLandingAnimation(gsap);
