class ValidatorView {
  static renderErrorField(validationState, fieldElement) {
    if (!fieldElement) {
      return;
    }

    if (validationState === 'valid') {
      fieldElement.classList.remove('has-error');
    }

    if (validationState === 'invalid') {
      fieldElement.classList.add('has-error');
    }
  }

  static renderInvalidAjaxForm(formId, invalidFieldsNames) {
    const formNode = document.querySelector(`#${formId}`);

    invalidFieldsNames.forEach((invalidFieldName) => {
      const fieldNode = formNode.querySelector(`[name=${invalidFieldName}]`);
      ValidatorView.renderErrorField('invalid', fieldNode);
    });
  }

  static renderValidAjaxForm(formId, fieldSelector) {
    const formNode = document.querySelector(`#${formId}`);
    const fieldNodes = Array.from(formNode.querySelectorAll(fieldSelector));

    fieldNodes.forEach((fieldNode) => {
      ValidatorView.renderErrorField('valid', fieldNode);
    });
  }
}

export default ValidatorView;
