import Gator from 'gator';

const HASH_MASK = 'section_';

class Anchor {
  static scrollToElement(fakeTargetNodeHash) {
    const normalizedFakeTargetNodeHash = fakeTargetNodeHash.replace('#', '');
    const realTargetNodeHash = `#${HASH_MASK}${normalizedFakeTargetNodeHash}`;
    const targetNode = document.querySelector(realTargetNodeHash);

    if (!targetNode) {
      throw new Error('[SPIKS] Anchor: targetNode is null');
    }

    if (window.spiks.smoothScroll) {
      window.spiks.smoothScroll.scrollTo(targetNode, {
        onComplete: () => {
          window.location.hash = fakeTargetNodeHash;
        },
      });
    } else {
      targetNode.scrollIntoView({
        behavior: 'smooth',
        block: 'start',
      });

      window.location.hash = fakeTargetNodeHash;
    }
  }

  static showInitialPageState() {
    try {
      const fakeTargetNodeHash = window.location.hash;
      if (fakeTargetNodeHash) {
        Anchor.scrollToElement(fakeTargetNodeHash);
        return;
      }
    } catch (e) {
      console.debug(e);
    }
  }

  constructor(nodeSelector) {
    this.nodeSelector = nodeSelector;

    this.attachEvents();
  }

  attachEvents() {
    Gator(document).on('click', this.nodeSelector, (event) => {
      event.preventDefault();

      if (window.spiks.mainMenu) {
        window.spiks.mainMenu.hide();
      }

      const { target } = event;
      const fakeTargetNodeHash = target.getAttribute('href');
      Anchor.scrollToElement(fakeTargetNodeHash);
    });
  }
}

export default Anchor;
