import Gator from 'gator';

class FileLoader {
  static buildResultTemplate(filename) {
    return (`                
      <div class="file-loader__item">
        <button class="file-loader__item-btn" type="button" data-action="remove-file">
          <span class="file-loader__item-icon" aria-hidden="true">
            <svg class="icon-svg icon-cancel">
              <use xlink:href="#icon-cancel"></use>
            </svg>
          </span>
          </button>
          <span class="file-loader__item-title">${filename}</span>
      </div>
    `);
  }

  constructor(element) {
    this.element = element;

    try {
      this.initState();
      this.findElements();
      this.attachEvents();
    } catch (e) {
      console.debug(e);
    }
  }

  initState() {
    this.state = {
      process: 'idle',
      inputValue: '',
      inputFilename: '',
    };
  }

  findElements() {
    this.inputElement = this.element.querySelector('[data-entity="input"]');
  }

  attachEvents() {
    this.inputElement.addEventListener('change', () => {
      this.state.inputValue = this.inputElement.value;
      this.state.inputFilename = this.inputElement.files.length ? this.inputElement.files[0].name : '';
      this.state.process = 'adding';

      this.renderResults();
    });

    Gator(this.element).on('click', '[data-action="remove-file"]', () => {
      this.state.inputValue = '';
      this.state.inputFilename = '';
      this.state.process = 'removing';

      this.renderResults();
    });
  }

  renderResults() {
    if (this.state.process === 'adding') {
      if (!this.resultElement) {
        this.resultElement = this.element.querySelector('[data-entity="results"]');
      }

      this.resultElement.innerHTML = this.state.inputFilename ? FileLoader.buildResultTemplate(this.state.inputFilename) : '';

      setTimeout(() => {
        this.element.classList.add('has-items');
      }, 300);
    }

    if (this.state.process === 'removing') {
      this.element.classList.remove('has-items');
      this.inputElement.value = this.state.inputValue;
    }
  }
}

export default FileLoader;
