class Menu {
  constructor(containerNodeSector) {
    this.containerNode = document.querySelector(containerNodeSector);

    try {
      this.findNodes();
      this.initState();
      this.initComponent();
    } catch (e) {
      console.debug(`[SPIKS] Menu: ${e}`);
    }
  }

  findNodes() {
    this.togglerNode = this.containerNode.querySelector('[data-entity="toggler"]');
    this.menuNode = this.containerNode.querySelector('[data-entity="menu"]');

    if (!this.togglerNode) {
      throw new Error('[SPIKS] Menu: togglerNode is null');
    }

    if (!this.menuNode) {
      throw new Error('[SPIKS] Menu: menuNode is null');
    }
  }

  initState() {
    this.state = {
      menuVisibility: 'hidden',
    };
  }

  initComponent() {
    this.attachEvents();
  }

  attachEvents() {
    this.togglerNode.addEventListener('click', this.togglerClickEvent.bind(this));
  }

  togglerClickEvent() {
    const { menuVisibility } = this.state;

    this.state.menuVisibility = menuVisibility === 'hidden' ? 'shown' : 'hidden';
    this.renderMenuVisibility();
  }

  renderMenuVisibility() {
    const { menuVisibility } = this.state;

    if (menuVisibility === 'hidden') {
      this.containerNode.classList.remove('header_type_full');
      this.containerNode.classList.add('header_type_compact');

      if (window.spiks.cursor && window.spiks.cursor.api) {
        window.spiks.cursor.api.removeState('-menu');
      }
    }

    if (menuVisibility === 'shown') {
      this.containerNode.classList.remove('header_type_compact');
      this.containerNode.classList.add('header_type_full');

      if (window.spiks.cursor && window.spiks.cursor.api) {
        window.spiks.cursor.api.addState('-menu');
      }
    }
  }

  hide() {
    this.state.menuVisibility = 'hidden';
    this.renderMenuVisibility();
  }
}

export default Menu;
