import Swiper from 'swiper';
import breakpoints from '@/constants/breakpoints.js';
import contentWidth from '@/constants/contentWidth.js';

class Slider {
  static calcHorizontalOffset() {
    const actualWindowWidth = window.innerWidth;
    const actualContentWidth = actualWindowWidth < breakpoints.desktop
      ? contentWidth.pad : contentWidth.desktop;

    return (actualWindowWidth - actualContentWidth) / 2;
  }

  static updateLazyLoadingModule() {
    if (window.spiks && window.spiks.lozadObserver) {
      window.spiks.lozadObserver.observe();
    }
  }

  constructor(containerNodeSelector, sliderOptions) {
    this.containerNode = document.querySelector(containerNodeSelector);
    this.sliderOptions = sliderOptions;

    try {
      this.findNodes();
      this.initState();
      this.initComponent();
    } catch (e) {
      console.debug(`[SPIKS] Slider: ${e}`);
    }
  }

  findNodes() {
    this.sliderNode = this.containerNode.querySelector('[data-entity="slider"]');
    this.shadowLeftNode = this.containerNode.querySelector('[data-entity="slider-shadow-left"]');
    this.shadowRightNode = this.containerNode.querySelector('[data-entity="slider-shadow-right"]');

    if (!this.sliderNode) {
      throw new Error('[SPIKS] Slider: sliderNode is null');
    }
  }

  initState() {
    this.state = {
      sliderProgress: 'idle',
    };
  }

  initComponent() {
    this.buildSlider();
    this.attachEvents();
  }

  buildSlider() {
    const slidesOffsetValue = Slider.calcHorizontalOffset();

    this.slider = new Swiper(this.sliderNode, {
      slidesOffsetBefore: slidesOffsetValue,
      slidesOffsetAfter: slidesOffsetValue,
      watchSlidesProgress: true,
      touchStartPreventDefault: false,
      speed: 1000,
      ...this.sliderOptions,
    });

    this.updateSliderCursorView();
  }

  attachEvents() {
    this.slider.on('afterInit', () => {
      if (window.spiks && window.spiks.lozadObserver) {
        window.spiks.lozadObserver.observe();
      }
    });
    this.slider.on('slideChange', this.sliderSlideChangeHandler.bind(this));
    this.slider.on('resize', this.sliderResizeHandler.bind(this));
  }

  sliderSlideChangeHandler() {
    this.updateSliderShadowsView();
  }

  sliderResizeHandler() {
    this.updateSliderOffsetValues();
    this.updateSliderShadowsView();
  }

  updateSliderOffsetValues() {
    const slidesOffsetValue = Slider.calcHorizontalOffset();

    this.slider.params.slidesOffsetBefore = slidesOffsetValue;
    this.slider.params.slidesOffsetAfter = slidesOffsetValue;

    this.slider.update();
  }

  updateSliderShadowsView() {
    if (this.slider.isBeginning) {
      this.state.sliderProgress = 'beginning';
    } else if (this.slider.isEnd) {
      this.state.sliderProgress = 'finished';
    } else {
      this.state.sliderProgress = 'processing';
    }

    this.renderSliderShadows();
  }

  renderSliderShadows() {
    const { sliderProgress } = this.state;

    if (sliderProgress === 'beginning') {
      this.shadowLeftNode.classList.add('hidden');
      this.shadowRightNode.classList.remove('hidden');
    }

    if (sliderProgress === 'finished') {
      this.shadowLeftNode.classList.remove('hidden');
      this.shadowRightNode.classList.add('hidden');
    }

    if (sliderProgress === 'processing') {
      this.shadowLeftNode.classList.remove('hidden');
      this.shadowRightNode.classList.remove('hidden');
    }
  }

  updateSliderCursorView() {
    if (this.slider.isLocked) {
      this.sliderNode.removeAttribute('data-cursor');
      return;
    }

    this.sliderNode.setAttribute('data-cursor', '-slider');
  }
}

export default Slider;
