import * as yup from 'yup';

class Validator {
  static validateText(value, min, max) {
    const scheme = yup
      .string()
      .trim()
      .required()
      .min(min)
      .max(max);

    return scheme.isValidSync(value);
  }

  static validateTel(value) {
    const phoneRegExp = /^((8|\+7)[- ]?)?(\(?\d{3}\)?[- ]?)?[\d\- ]{7,10}$/;

    const scheme = yup
      .string()
      .trim()
      .required()
      .matches(phoneRegExp);

    return scheme.isValidSync(value);
  }

  static validateEmail(value) {
    const scheme = yup
      .string()
      .trim()
      .required()
      .email();

    return scheme.isValidSync(value);
  }
}

export default Validator;
