import gsap from 'gsap';

class MagneticNode {
  constructor(containerNode, contentNodeSelector) {
    this.containerNode = containerNode;
    this.contentNodeSelector = contentNodeSelector;

    try {
      this.findNodes();
      this.calcPosition();
      this.attachEvents();
    } catch (e) {
      console.debug(`[SPIKS] MagneticNode: ${e}`);
    }
  }

  findNodes() {
    this.contentNode = this.containerNode.querySelector(this.contentNodeSelector);
    this.movement = this.containerNode.dataset.parallaxMovement;
  }

  calcPosition() {
    const {
      top: containerNodeRelativeTop,
      left: containerNodeRelativeLeft,
    } = this.containerNode.getBoundingClientRect();

    this.containerNodeWidth = this.containerNode.clientWidth;
    this.containerNodeHeight = this.containerNode.clientHeight;
    this.containerNodeAbsoluteTop = containerNodeRelativeTop + window.pageYOffset;
    this.containerNodeAbsoluteLeft = containerNodeRelativeLeft + window.pageXOffset;
  }

  calcMovementPosition(eventPageX, eventPageY) {
    const relX = eventPageX - this.containerNodeAbsoluteLeft;
    const relY = eventPageY - this.containerNodeAbsoluteTop;
    const movement = Number(this.movement);

    this.absX = ((relX - this.containerNodeWidth / 2) / this.containerNodeWidth) * movement;
    this.absY = ((relY - this.containerNodeHeight / 2) / this.containerNodeHeight) * movement;
  }

  attachEvents() {
    this.containerNode.addEventListener('mousemove', (event) => {
      this.calcMovementPosition(event.pageX, event.pageY);

      gsap.to(this.contentNode, {
        x: this.absX,
        y: this.absY,
        duration: 1,
      });
    });

    this.containerNode.addEventListener('mouseenter', () => {
      this.calcPosition();
      gsap.to(this.contentNode, {
        scale: 1.02,
      });
    });

    this.containerNode.addEventListener('mouseleave', () => {
      gsap.to(this.contentNode, {
        x: 0,
        y: 0,
        scale: 1,
      });
    });
  }
}

export default MagneticNode;
