import Slider from '@components/slider/Slider.js';

export default () => {
  const slidersMap = [
    {
      containerNodeSelector: '[data-component="slider-cases"]',
      sliderOptions: {
        slidesPerView: 'auto',
        spaceBetween: 20,
      },
    },
    {
      containerNodeSelector: '[data-component="slider-reviews"]',
      sliderOptions: {
        slidesPerView: 'auto',
        spaceBetween: 20,
      },
    },
  ];

  slidersMap.forEach(({ containerNodeSelector, sliderOptions }) => {
    new Slider(containerNodeSelector, sliderOptions);
  });
};
