const initComponent = (componentNode) => {
	const component = componentNode;
	const body = component.querySelector('[data-vacancy="body"]');
	const id = component.getAttribute('id');
    let pathname = location.pathname.replaceAll('/', '');
    let hash = location.hash.replaceAll('#', '');
    let currentId = hash != '' ? hash : (pathname != '' ? `${pathname}` : '');
	
	const changeHash = (id) => {
		let thisId = id ? id : '/';
		history.pushState('', '', `${thisId}`);
	}
	
	if (!body) {
		// console.debug()
		return;
	}
	
	const header = component.querySelector('[data-vacancy="header"]');
	const inner = component.querySelector('[data-vacancy="inner"]');

	const observer = new ResizeObserver(() => {
		if (component.classList.contains('is-open') ) {
			body.style.maxHeight = inner.offsetHeight + 'px';
		}
	}).observe(inner)

	const check = (current) => {
		document.querySelectorAll('[data-component="vacancy"]').forEach(item => {
			const body = item.querySelector('[data-vacancy="body"]');
			const inner = item.querySelector('[data-vacancy="inner"]');

			if(current != item) {
				item.classList.remove('is-open')
				body.style.maxHeight = '';

			} else if (item.classList.contains('is-open')) {
				body.style.maxHeight = inner.offsetHeight + 'px';
				changeHash(item.getAttribute('id'))
			} else {
				changeHash()
				body.style.maxHeight = '';
			}
		})
	}
	
	header.onclick = () => {
		component.classList.toggle('is-open');
		check(component)
	}

	if(currentId == id) {
		component.classList.add('is-open');
		// check(component)
	}
};
  
  
const initVacancy = () => {
	const componentNodes = Array.from(document.querySelectorAll('[data-component="vacancy"]'));

	componentNodes.forEach(initComponent);
}

document.addEventListener('DOMContentLoaded', () => {
	initVacancy()
});
  
export default initVacancy;