import MicroModal from 'micromodal';
import lozad from 'lozad';
import registerValidatorComponent from '@components/validator/script.js';
import registerModalComponent from '@components/modal/script.js';
import registerMenuComponent from '@components/header/script.js';
import Cookies from '@components/cookies/Cookies.js';
import MagneticNode from '@components/magneticNode/MagneticNode';
import Cursor from '@components/cursor/Cursor.js';
import Anchor from '@components/anchor/Anchor.js';
import FileLoader from '@components/file-loader/FileLoader.js';

import setupGsap from '@/init/common/configuration/gsap.js';
import pageAnimation from '@/init/common/animation/page.js';
import mainPageAnimation from '@/init/common/animation/mainPage.js';
import detailPageAnimation from '@/init/common/animation/detailPage.js';
import errorPageAnimation from '@/init/common/animation/errorPage.js';
import mainIntroAnimation from '@/init/common/animation/mainIntro.js';
import Marquee from '@components/marquee/Marquee.js';
import vacancy from '@components/vacancy/script.js';
import link from '@components/link/script.js';

export default () => {
  // конфигурация gsap
  const configuredGsap = setupGsap();

  // инициализация MicroModal плагина
  MicroModal.init();

  // регистрация кастомных компонентов
  window.spiks = {};
  registerValidatorComponent();
  registerModalComponent();
  registerMenuComponent();

  // инициализация кукисов
  new Cookies();

  // инициализация параллакс-кнопок
  const magneticNodes = Array.from(document.querySelectorAll('[data-component="magnetic-button"]'));
  magneticNodes.forEach((node) => {
    new MagneticNode(node, '[data-entity="button"]');
  });

  // инициализация кастомного курсора
  if (window.spiks && !window.spiks.cursor) {
    window.spiks.cursor = new Cursor('body', {
      stateDetection: {
        '-menu': '[data-cursor="-menu"]',
        '-project': '[data-cursor="-project"]',
        '-slider': '[data-cursor="-slider"]',
      },
    });
  }

  // инициализация вакансии
  if (window.spiks && !window.spiks.vacancy) {
    window.spiks.vacancy = vacancy;
  }

  // инициализация ссылок
  if (window.spiks && !window.spiks.link) {
    window.spiks.link = link;
  }

  // инициализация отложенной загрузки изображений
  if (window.spiks) {
    window.spiks.lozadObserver = lozad();
    window.spiks.lozadObserver.observe();
  }

  // инициализация якорей
  const anchorNode = document.querySelector('[data-entity="anchor"]');
  if (anchorNode) {
    new Anchor('[data-entity="anchor"]');
  }

  // инициализация FileLoader
  const fieldsTypeFile = Array.from(document.querySelectorAll('[data-component="request-file"]'));
  fieldsTypeFile.forEach((field) => {
    new FileLoader(field);
  });

  if (window.spiks) {
    window.spiks.fileLoadingManager = {
      init: (element) => {
        new FileLoader(element);
      },
    };
  }

  // инициализация бегущих строк
  const marqueeElements = Array.from(document.querySelectorAll('[data-component="marquee"]'));

  marqueeElements.forEach((element) => {
    new Marquee(element, configuredGsap);
  });

  // инициализация общей анимации
  pageAnimation(configuredGsap);

  // инициализация анимации главного лендинга
  mainPageAnimation(configuredGsap);
  Anchor.showInitialPageState();

  // инициализация анимации детального лендинга
  detailPageAnimation(configuredGsap);

  // инициализация анимации 404
  errorPageAnimation();
};
