import gsap from 'gsap';
import MouseFollower from 'mouse-follower';
import { debounce } from 'lodash';
import breakpoints from '@/constants/breakpoints.js';

MouseFollower.registerGSAP(gsap);

class Cursor {
  constructor(containerNodeSelector, mouseFollowerOptions = {}) {
    this.containerNode = document.querySelector(containerNodeSelector);

    this.mouseFollowerOptions = {
      container: this.containerNode,
      ...mouseFollowerOptions,
    };

    this.cursor = null;

    try {
      this.initComponent();
    } catch (e) {
      console.debug(`[SPIKS] Cursor: ${e}`);
    }
  }

  initComponent() {
    this.initCursor();
    this.attachEvents();
  }

  buildCursor() {
    this.cursor = new MouseFollower(this.mouseFollowerOptions);
  }

  attachEvents() {
    window.addEventListener('resize', debounce(this.windowResizeHandler.bind(this), 400));
  }

  windowResizeHandler() {
    this.initCursor();
  }

  initCursor() {
    if (window.innerWidth < breakpoints.pad && this.cursor) {
      const cursorNode = this.containerNode.querySelector('.mf-cursor');
      this.cursor.destroy();
      this.cursor = null;
      cursorNode.remove();
      return false;
    }

    if (window.innerWidth < breakpoints.pad || this.cursor) {
      return false;
    }

    this.buildCursor();
    return true;
  }

  get api() {
    return this.cursor;
  }
}

export default Cursor;
