import MicroModal from 'micromodal';

class ModalView {
  static renderSuccessMessage(options = {}) {
    const { contextElement, messageTemplate } = options;

    try {
      contextElement.innerHTML = messageTemplate;
    } catch (e) {
      console.debug(`[ModalView::renderSuccessMessage]: ${e}`);
    }
  }

  static showModal(modalId) {
    try {
      if (modalId) {
        MicroModal.show(modalId);
      }
    } catch (e) {
      console.debug(`[ModalView::showModal]: ${e}`);
    }
  }

  static hideModal(options = {}) {
    const { modalId, useTimeout, timeoutValue } = options;
    const timeoutFinallyValue = timeoutValue || 300;

    try {
      if (useTimeout) {
        setTimeout(() => {
          MicroModal.close(modalId);
        }, timeoutFinallyValue);
        return true;
      }

      MicroModal.close(modalId);
    } catch (e) {
      console.debug(`[ModalView::hideModal]: ${e}`);
    }

    return true;
  }
}

export default ModalView;
