const commonAnimation = (gsap) => {
  const renderPageLoading = () => {
    gsap.to(document.documentElement, {
      opacity: 1,
      delay: 0.2,
    });
  };

  const renderBurger = (burger, triggeringBurgerSections) => {
    triggeringBurgerSections.forEach((triggeringBurgerSection) => {
      gsap.to(burger, {
        scrollTrigger: {
          trigger: triggeringBurgerSection,
          start: 'top-=50 top',
          end: 'bottom-=50 top',

          onToggle: (self) => {
            if (self.isActive) {
              burger.classList.add('header__toggler_revert');
            } else {
              burger.classList.remove('header__toggler_revert');
            }
          },
        },
      });
    });
  };

  try {
    const triggeringBurgerSections = Array.from(document.querySelectorAll('[data-anim-action="revert-burger-colors"]'));
    const burger = document.querySelector('[data-anim-entity="burger"]');

    renderPageLoading();
    renderBurger(burger, triggeringBurgerSections);
  } catch (e) {
    console.debug(e);
  }
};

export default (gsap) => {
  commonAnimation(gsap);
};
