import { ScrollTrigger } from 'gsap/ScrollTrigger';

const detailLandingAnimation = (gsap) => {
  const findElements = () => ({
    navigationButton: document.querySelector('[data-anim-entity="intro-screen-button"]'),

    introScreen: {
      trigger: document.querySelectorAll('[data-anim-trigger="intro-screen"]'),
      screen: document.querySelector('[data-anim-entity="intro-screen"]'),
    },

    pictureScreen: {
      trigger: document.querySelectorAll('[data-anim-trigger="picture-screen"]'),
      screen: document.querySelector('[data-anim-entity="picture-screen"]'),
    },

    mainScreen: {
      trigger: document.querySelectorAll('[data-anim-trigger="picture-screen"]'),
      screen: document.querySelector('[data-anim-entity="main-screen"]'),
    },
  });

  const showSection = ({
    screen, animationParams,
  }) => {
    gsap.to(screen, {
      zIndex: 100,
      translateX: 0,
      translateY: 0,
      opacity: 1,
      ...animationParams,
    });
  };

  const hideSection = ({
    screen, animationParams,
  }) => {
    gsap.to(screen, {
      opacity: 0,
      zIndex: 0,
      ...animationParams,
    });
  };

  const buildScrollTriggerObserver = ({
    trigger, triggerParams,
  }) => {
    ScrollTrigger.create({
      trigger,
      start: 'top top',

      ...triggerParams,
    });
  };

  const attachEvents = ({ navigationButton }) => {
    navigationButton.addEventListener('click', () => {
      gsap.to(window, {
        overwrite: true,
        scrollTo: {
          y: 500,
          autoKill: true,
        },
      });
    });
  };

  const renderIntroScreen = ({ introScreen }) => {
    const {
      trigger,
      screen,
    } = introScreen;

    buildScrollTriggerObserver({
      trigger,
      triggerParams: {
        onEnterBack: () => showSection({ screen }),
        onLeave: () => hideSection({
          screen,
          animationParams: {
            translateY: '-100%',
          },
        }),
      },
    });
  };

  const renderPictureScreen = ({ pictureScreen }) => {
    const {
      trigger,
      screen,
    } = pictureScreen;

    buildScrollTriggerObserver({
      trigger,
      triggerParams: {
        onEnter: () => showSection({ screen }),
        onEnterBack: () => showSection({ screen }),
        onLeave: () => hideSection({
          screen,
          animationParams: {
            translateY: '-100%',
          },
        }),
        onLeaveBack: () => hideSection({
          screen,
          animationParams: {
            translateY: '100%',
          },
        }),
      },
    });
  };

  const renderMainScreen = ({ mainScreen }) => {
    const {
      trigger,
      screen,
    } = mainScreen;

    buildScrollTriggerObserver({
      trigger,
      triggerParams: {
        onEnterBack: () => hideSection({
          screen,
          animationParams: {
            opacity: 0,
            translateY: '320px',
          },
        }),

        onLeave: () => {
          showSection({
            screen,
            animationParams: {
              opacity: 1,
            },
          });
        },
      },
    });
  };

  try {
    const elements = findElements();

    attachEvents(elements);
    renderIntroScreen(elements);
    renderPictureScreen(elements);
    renderMainScreen(elements);
  } catch (e) {
    console.debug(e);
  }
};

export default (gsap) => detailLandingAnimation(gsap);
