import Menu from '@components/header/Menu.js';

const registerMenuComponent = () => {
  const mainMenu = new Menu('[data-component="header"]');

  window.spiks.mainMenu = mainMenu;
};

document.addEventListener('DOMContentLoaded', () => {
  let logo = document.querySelector('.header__logo');
  let projects = document.querySelector('.m-projects');

  document.addEventListener('scroll', () =>{
    let offset = window.innerHeight;

    if (projects) {
      offset = projects.offsetHeight + projects.offsetTop;
    }
    
    if(offset < window.pageYOffset){
      logo.classList.add('is-visible')
    } else{
      logo.classList.remove('is-visible')
    }
  })
})


export default registerMenuComponent;
